.wrapper {
	position: fixed;
    height: 100%;
    width: 100%;
	z-index: 100;
	background: rgba(253, 253, 253, 0.76);
}

.cssload-loading {
	position: absolute;
	top: 50%;
	left: 50%;
	width: 22px;
	height: 22px;
	margin: -11px 0 0 -11px;
}

.cssload-loading i {
	position: absolute;
	top: 22px; left: 22px;
	display: block;
	width: 40px;
	height: 40px;
	background: #ffd940;
	border-radius: 50px;
	animation: cssload-spin2 1.425s ease-in-out infinite;
		-o-animation: cssload-spin2 1.425s ease-in-out infinite;
		-ms-animation: cssload-spin2 1.425s ease-in-out infinite;
		-webkit-animation: cssload-spin2 1.425s ease-in-out infinite;
		-moz-animation: cssload-spin2 1.425s ease-in-out infinite;
}
.cssload-loading i:first-child {
	top: -22px; left: 0;
	background: #5a78ed;
	animation: cssload-spin 1.425s ease-in-out infinite;
		-o-animation: cssload-spin 1.425s ease-in-out infinite;
		-ms-animation: cssload-spin 1.425s ease-in-out infinite;
		-webkit-animation: cssload-spin 1.425s ease-in-out infinite;
		-moz-animation: cssload-spin 1.425s ease-in-out infinite;
}
.cssload-loading i:last-child {
	top: 22px; left: -22px;
	background: #ff6666;
	animation: cssload-spin3 1.425s ease-in-out infinite;
		-o-animation: cssload-spin3 1.425s ease-in-out infinite;
		-ms-animation: cssload-spin3 1.425s ease-in-out infinite;
		-webkit-animation: cssload-spin3 1.425s ease-in-out infinite;
		-moz-animation: cssload-spin3 1.425s ease-in-out infinite;
}






@keyframes cssload-spin {
	0% {
		top: -22px;
		left: 0;
		transform: scale(1);
	}
	17% {
		transform: scale(.5);
	}
	33% {
		top: 22px;
		left: 22px;
		transform: scale(1);
	}
	50% {
		transform: scale(.5);
	}
	66% {
		top: 22px;
		left: -22px;
		transform: scale(1);
	}
	83% {
		transform: scale(.5);
	}
	100% {
		top: -22px;
		left: 0;
		transform: scale(1);
	}
}

